:root {
  --primary-color: #0066ff;
  --secondary-color: #00d4ff;
  --accent-color: #ff6b35;
  --success-color: #00ff88;
  --warning-color: #ffaa00;
  --danger-color: #ff4757;
  --whatsapp-color: #25d366;
  --facebook-color: #1877f2;
  --instagram-color: #e4405f;
  --dark-bg: #0a0a0f;
  --dark-secondary: #1a1a2e;
  --dark-tertiary: #16213e;
  --text-primary: #ffffff;
  --text-secondary: #b0b3c7;
  --gradient-primary: linear-gradient(135deg, #0066ff, #00d4ff);
  --gradient-accent: linear-gradient(135deg, #ff6b35, #ff8e53);
  --gradient-success: linear-gradient(135deg, #00ff88, #00cc6a);
  --gradient-whatsapp: linear-gradient(135deg, #25d366, #128c7e);
  --gradient-facebook: linear-gradient(135deg, #1877f2, #42a5f5);
  --gradient-instagram: linear-gradient(135deg, #e4405f, #fd1d1d, #fcb045);
  --gradient-dark: linear-gradient(135deg, #0a0a0f, #1a1a2e);
}

.profession-selector-section {
  padding: 8rem 2rem;
  background: var(--gradient-dark);
  position: relative;
  overflow: hidden;
}
.profession-selector-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 20% 30%, rgba(0, 102, 255, 0.1) 0%, transparent 50%), radial-gradient(circle at 80% 70%, rgba(255, 107, 53, 0.1) 0%, transparent 50%);
  animation: professionGlow 8s ease-in-out infinite alternate;
}

@keyframes professionGlow {
  0% {
    opacity: 0.3;
    transform: scale(1);
  }
  100% {
    opacity: 0.6;
    transform: scale(1.1);
  }
}
.profession-container {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.profession-scroll-container {
  margin: 8rem 0;
  position: relative;
}
.profession-scroll-container::before, .profession-scroll-container::after {
  content: "";
  position: absolute;
  top: 0;
  width: 60px;
  height: 100%;
  z-index: 10;
  pointer-events: none;
}
.profession-scroll-container::before {
  left: 0;
  background: linear-gradient(to right, var(--dark-bg), transparent);
}
.profession-scroll-container::after {
  right: 0;
  background: linear-gradient(to left, var(--dark-bg), transparent);
}

.profession-cards {
  display: flex;
  gap: 2rem;
  overflow-x: auto;
  scroll-behavior: smooth;
  padding: 1rem 4rem;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.profession-cards::-webkit-scrollbar {
  display: none;
}

.profession-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 30px;
  padding: 4rem;
  min-width: 200px;
  flex-shrink: 0;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}
.profession-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.5s;
}
.profession-card:hover {
  transform: translateY(-10px);
  border-color: rgba(0, 102, 255, 0.4);
  box-shadow: 0 20px 60px rgba(0, 102, 255, 0.2);
}
.profession-card:hover::before {
  left: 100%;
}
.profession-card.active {
  border-color: var(--primary-color);
  background: rgba(0, 102, 255, 0.1);
  transform: translateY(-5px);
}
.profession-card.active .profession-icon {
  transform: scale(1.2);
}

.profession-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  transition: transform 0.3s ease;
  display: block;
}

.profession-title {
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.profession-count {
  font-size: 0.9rem;
  color: var(--text-secondary);
  opacity: 0.8;
}

.profession-content {
  margin-top: 8rem;
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.8s ease-out;
}
.profession-content.visible {
  opacity: 1;
  transform: translateY(0);
}

.content-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 30px;
  padding: 8rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
}
.content-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 30% 20%, rgba(0, 102, 255, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.content-header {
  display: flex;
  align-items: center;
  margin-bottom: 4rem;
  position: relative;
  z-index: 2;
}

.content-icon {
  width: 80px;
  height: 80px;
  background: var(--gradient-primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  margin-right: 2rem;
  transition: all 0.3s ease;
  animation: pulse 2s ease-in-out infinite;
}
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 0 20px rgba(0, 102, 255, 0.3);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 30px rgba(0, 102, 255, 0.3);
  }
}

.content-header h3 {
  font-size: 2rem;
  font-weight: 900;
  color: var(--text-primary);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.content-description {
  position: relative;
  z-index: 2;
}
.content-description p {
  font-size: 1.2rem;
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 4rem;
}

.content-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-bottom: 8rem;
  position: relative;
  z-index: 2;
}

.feature-item {
  display: flex;
  align-items: center;
  padding: 1rem 2rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  color: var(--text-primary);
  font-weight: 500;
  transition: all 0.3s ease;
}
.feature-item:hover {
  background: rgba(255, 255, 255, 0.08);
  transform: translateX(5px);
}

.content-cta {
  text-align: center;
  position: relative;
  z-index: 2;
}

.profession-cta-btn {
  background: var(--gradient-primary);
  color: white;
  padding: 2rem 8rem;
  border-radius: 50px;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  display: inline-block;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}
.profession-cta-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 102, 255, 0.4);
}
.profession-cta-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}
.profession-cta-btn:hover::before {
  left: 100%;
}
.profession-cta-btn {
  position: relative;
  overflow: hidden;
}
.profession-cta-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}
.profession-cta-btn:hover::before {
  left: 100%;
}

@media (max-width: 767px) {
  .profession-selector-section {
    padding: 4rem 1rem;
  }
  .profession-scroll-container::before, .profession-scroll-container::after {
    width: 30px;
  }
  .profession-cards {
    padding: 1rem;
    gap: 1rem;
  }
  .profession-card {
    min-width: 160px;
    padding: 2rem;
  }
  .profession-icon {
    font-size: 2.5rem;
  }
  .profession-title {
    font-size: 1rem;
  }
  .content-card {
    padding: 2rem;
  }
  .content-header {
    flex-direction: column;
    text-align: center;
  }
  .content-header h3 {
    font-size: 1.5rem;
    margin-top: 1rem;
  }
  .content-icon {
    width: 60px;
    height: 60px;
    font-size: 2rem;
    margin-right: 0;
  }
  .content-features {
    grid-template-columns: 1fr;
  }
  .profession-cta-btn {
    padding: 1rem 2rem;
    font-size: 1rem;
  }
}