@use 'variables' as v;

@mixin glassmorphism($opacity: 0.05, $blur: 20px) {
    background: rgba(255, 255, 255, $opacity);
    backdrop-filter: blur($blur);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

@mixin gradient-text($gradient) {
    background: $gradient;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

@mixin button-hover($shadow-color: rgba(0, 102, 255, 0.4)) {
    transition: all v.$transition-normal;
    position: relative;
    overflow: hidden;
    
    &:hover {
        transform: translateY(-3px);
        box-shadow: 0 15px 40px $shadow-color;
    }
    
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
        transition: left 0.5s;
    }
    
    &:hover::before {
        left: 100%;
    }
}

@mixin card-hover($scale: 1.02, $shadow-color: rgba(0, 102, 255, 0.2)) {
    transition: all v.$transition-slow;
    cursor: pointer;
    
    &:hover {
        transform: translateY(-15px) scale($scale);
        box-shadow: 0 30px 80px $shadow-color;
    }
}

@mixin floating-animation($duration: 6s, $delay: 0s) {
    animation: float $duration ease-in-out infinite;
    animation-delay: $delay;
}

@mixin fade-in-animation($delay: 0s) {
    opacity: 0;
    transform: translateY(30px);
    transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
    
    &.visible {
        opacity: 1;
        transform: translateY(0);
    }
}

@mixin pulse-animation($color: rgba(0, 102, 255, 0.3), $duration: 2s) {
    animation: pulse $duration ease-in-out infinite;
    
    @keyframes pulse {
        0%, 100% { 
            transform: scale(1); 
            box-shadow: 0 0 20px $color; 
        }
        50% { 
            transform: scale(1.05); 
            box-shadow: 0 0 30px $color; 
        }
    }
}

@mixin rotating-border($color: rgba(255, 255, 255, 0.1), $duration: 3s) {
    &::before {
        content: '';
        position: absolute;
        top: -2px;
        left: -2px;
        right: -2px;
        bottom: -2px;
        border-radius: inherit;
        padding: 2px;
        background: linear-gradient(45deg, transparent, $color, transparent);
        mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
        mask-composite: xor;
        animation: borderRotate $duration linear infinite;
        opacity: 0;
        transition: opacity v.$transition-normal;
    }
    
    &:hover::before {
        opacity: 1;
    }
    
    @keyframes borderRotate {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }
}

@mixin mobile {
    @media (max-width: #{v.$mobile - 1px}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{v.$mobile}) and (max-width: #{v.$tablet - 1px}) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: #{v.$tablet}) {
        @content;
    }
}