@use '../base/variables' as v;
@use '../base/mixins' as m;

.profession-selector-section {
    padding: v.$spacing-xl v.$spacing-md;
    background: var(--gradient-dark);
    position: relative;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at 20% 30%, rgba(0, 102, 255, 0.1) 0%, transparent 50%),
                    radial-gradient(circle at 80% 70%, rgba(255, 107, 53, 0.1) 0%, transparent 50%);
        animation: professionGlow 8s ease-in-out infinite alternate;
    }
}

@keyframes professionGlow {
    0% { opacity: 0.3; transform: scale(1); }
    100% { opacity: 0.6; transform: scale(1.1); }
}

.profession-container {
    max-width: v.$desktop;
    margin: 0 auto;
    position: relative;
    z-index: 2;
}

.profession-scroll-container {
    margin: v.$spacing-xl 0;
    position: relative;
    
    &::before,
    &::after {
        content: '';
        position: absolute;
        top: 0;
        width: 60px;
        height: 100%;
        z-index: 10;
        pointer-events: none;
    }
    
    &::before {
        left: 0;
        background: linear-gradient(to right, var(--dark-bg), transparent);
    }
    
    &::after {
        right: 0;
        background: linear-gradient(to left, var(--dark-bg), transparent);
    }
}

.profession-cards {
    display: flex;
    gap: v.$spacing-md;
    overflow-x: auto;
    scroll-behavior: smooth;
    padding: v.$spacing-sm v.$spacing-lg;
    scrollbar-width: none;
    -ms-overflow-style: none;
    
    &::-webkit-scrollbar {
        display: none;
    }
}

.profession-card {
    @include m.glassmorphism();
    border: 2px solid rgba(255, 255, 255, 0.1);
    border-radius: v.$radius-lg;
    padding: v.$spacing-lg;
    min-width: 200px;
    flex-shrink: 0;
    text-align: center;
    cursor: pointer;
    transition: all v.$transition-normal;
    position: relative;
    overflow: hidden;
    
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
        transition: left 0.5s;
    }
    
    &:hover {
        transform: translateY(-10px);
        border-color: rgba(0, 102, 255, 0.4);
        box-shadow: 0 20px 60px rgba(0, 102, 255, 0.2);
        
        &::before {
            left: 100%;
        }
    }
    
    &.active {
        border-color: var(--primary-color);
        background: rgba(0, 102, 255, 0.1);
        transform: translateY(-5px);
        
        .profession-icon {
            transform: scale(1.2);
        }
    }
}

.profession-icon {
    font-size: 3rem;
    margin-bottom: v.$spacing-sm;
    transition: transform v.$transition-normal;
    display: block;
}

.profession-title {
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--text-primary);
    margin-bottom: v.$spacing-xs;
}

.profession-count {
    font-size: 0.9rem;
    color: var(--text-secondary);
    opacity: 0.8;
}

.profession-content {
    margin-top: v.$spacing-xl;
    opacity: 0;
    transform: translateY(20px);
    transition: all 0.8s ease-out;
    
    &.visible {
        opacity: 1;
        transform: translateY(0);
    }
}

.content-card {
    @include m.glassmorphism();
    border-radius: v.$radius-lg;
    padding: v.$spacing-xl;
    border: 1px solid rgba(255, 255, 255, 0.1);
    position: relative;
    overflow: hidden;
    
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at 30% 20%, rgba(0, 102, 255, 0.1) 0%, transparent 50%);
        pointer-events: none;
    }
}

.content-header {
    display: flex;
    align-items: center;
    margin-bottom: v.$spacing-lg;
    position: relative;
    z-index: 2;
}

.content-icon {
    width: 80px;
    height: 80px;
    background: var(--gradient-primary);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2.5rem;
    margin-right: v.$spacing-md;
    transition: all v.$transition-normal;
    @include m.pulse-animation(rgba(0, 102, 255, 0.3));
}

.content-header h3 {
    font-size: 2rem;
    font-weight: 900;
    color: var(--text-primary);
    @include m.gradient-text(var(--gradient-primary));
}

.content-description {
    position: relative;
    z-index: 2;
    
    p {
        font-size: 1.2rem;
        color: var(--text-secondary);
        line-height: 1.6;
        margin-bottom: v.$spacing-lg;
    }
}

.content-features {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: v.$spacing-md;
    margin-bottom: v.$spacing-xl;
    position: relative;
    z-index: 2;
}

.feature-item {
    display: flex;
    align-items: center;
    padding: v.$spacing-sm v.$spacing-md;
    @include m.glassmorphism(0.1);
    border-radius: v.$radius-sm;
    color: var(--text-primary);
    font-weight: 500;
    transition: all v.$transition-normal;
    
    &:hover {
        background: rgba(255, 255, 255, 0.08);
        transform: translateX(5px);
    }
}

.content-cta {
    text-align: center;
    position: relative;
    z-index: 2;
}

.profession-cta-btn {
    background: var(--gradient-primary);
    color: white;
    padding: v.$spacing-md v.$spacing-xl;
    border-radius: v.$radius-pill;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.1rem;
    display: inline-block;
    @include m.button-hover();
    position: relative;
    overflow: hidden;
    
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
        transition: left 0.5s;
    }
    
    &:hover::before {
        left: 100%;
    }
}

@include m.mobile {
    .profession-selector-section {
        padding: v.$spacing-lg v.$spacing-sm;
    }
    
    .profession-scroll-container {
        &::before,
        &::after {
            width: 30px;
        }
    }
    
    .profession-cards {
        padding: v.$spacing-sm;
        gap: v.$spacing-sm;
    }
    
    .profession-card {
        min-width: 160px;
        padding: v.$spacing-md;
    }
    
    .profession-icon {
        font-size: 2.5rem;
    }
    
    .profession-title {
        font-size: 1rem;
    }
    
    .content-card {
        padding: v.$spacing-md;
    }
    
    .content-header {
        flex-direction: column;
        text-align: center;
        
        h3 {
            font-size: 1.5rem;
            margin-top: v.$spacing-sm;
        }
    }
    
    .content-icon {
        width: 60px;
        height: 60px;
        font-size: 2rem;
        margin-right: 0;
    }
    
    .content-features {
        grid-template-columns: 1fr;
    }
    
    .profession-cta-btn {
        padding: v.$spacing-sm v.$spacing-md;
        font-size: 1rem;
    }
}